 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block;\n}\n/* HTML5 媒体文件跟 img 保持一致 */\naudio,\ncanvas,\nvideo {\n  display: inline-block;\n  *display: inline;\n  *zoom: 1;\n}\n/* 要注意表单元素并不继承父级 font 的问题 */\nbody,\nbutton,\ninput,\nselect,\ntextarea {\n  font: 12px/1.62 Helvetica, Arial, sans-serif;\n}\ninput,\nselect,\ntextarea {\n  outline: none;\n  font-size: 100%;\n}\n/* 去掉各Table  cell 的边距并让其边重合 */\ntable {\n  border-spacing: 0;\n  border-collapse: collapse;\n}\n/* IE bug fixed: th 不继承 text-align*/\nth {\n  text-align: inherit;\n  font-weight: bold;\n}\n/* 去除默认边框 */\nfieldset,\nimg {\n  border: 0;\n}\n/* ie6 7 8(q) bug 显示为行内表现 */\niframe {\n  display: block;\n}\n/* 去掉 firefox 下此元素的边框 */\nabbr,\nacronym {\n  border: 0;\n  font-variant: normal;\n}\n/* 一致的 del 样式 */\ndel {\n  text-decoration: line-through;\n}\naddress,\ncaption,\ncite,\ncode,\ndfn,\nem,\nth,\nvar {\n  font-weight: 500;\n  font-style: normal;\n}\n/* 去掉列表前的标识, li 会继承 */\nol,\nul {\n  list-style: none;\n}\n/* 对齐是排版最重要的因素, 别让什么都居中 */\ncaption,\nth {\n  text-align: left;\n}\n/* 来自yahoo, 让标题都自定义, 适应多个系统应用 */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-size: 100%;\n  font-weight: 500;\n}\nq:before,\nq:after {\n  content: '';\n}\n/* 统一上标和下标 */\nsub,\nsup {\n  position: relative;\n  vertical-align: baseline;\n  font-size: 75%;\n  line-height: 0;\n}\nsup {\n  top: -0.5em;\n}\nsub {\n  bottom: -0.25em;\n}\n/* 正常链接 未访问 */\na:link,\na:visited {\n  color: #08c;\n}\n/*红色的链接*/\na.redlink:link,\na.redlink:visited {\n  color: #910004;\n}\n/* 鼠标悬停 */\na:hover {\n  text-decoration: underline;\n}\n/* 默认不显示下划线，保持页面简洁 */\nins,\na {\n  text-decoration: none;\n}\n/* 代码字体 */\ncode,\nkbd,\npre,\nsamp {\n  font-family: monospace, serif;\n  font-size: 1em;\n}\n/* 清理浮动 */\n.clearfix:after {\n  display: block;\n  visibility: hidden;\n  clear: both;\n  height: 0;\n  content: \" \";\n  font-size: 0;\n}\n.clearfix {\n  zoom: 1;\n  /* for IE6 IE7 */\n}\n/* 隐藏, 通常用来与 JS 配合 */\n.fn-hide {\n  display: none;\n}\n/* 设置内联, 减少浮动带来的bug */\n.fn-left,\n.fn-right {\n  display: inline;\n}\n.fn-left {\n  float: left;\n}\n.fn-right {\n  float: right;\n}\n/* 单行文字溢出时出现省略号，需设定宽度 */\n.fn-text-overflow {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n/* 简单的渐变 */\n.fn-linear {\n  background: linear-gradient(top, #fbfbfb, #f2f2f2);\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fbfbfb', endColorstr='#f2f2f2');\n}\n/* 浅色的渐变 */\n.fn-linear-light {\n  background: linear-gradient(top, #fdfdfd, #eee);\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fdfdfd', endColorstr='#eee');\n}\n/* chrome 下字体过小的问题 */\n.fn-webkit-adjust {\n  -webkit-text-size-adjust: none;\n}\n/* placeholder颜色*/\n::-webkit-input-placeholder {\n  /* WebKit browsers */\n  color: #999;\n}\n:-moz-placeholder {\n  /* Mozilla Firefox 4 to 18 */\n  color: #999;\n}\n::-moz-placeholder {\n  /* Mozilla Firefox 19+ */\n  color: #999;\n}\n:-ms-input-placeholder {\n  /* Internet Explorer 10+ */\n  color: #999;\n}\n/*一些页面宽度*/\n.grid-950 {\n  width: 950px;\n  margin: 0 auto;\n}\n/*.container{\n    padding-top: 10px;\n    background: #f3f3f3;\n    min-width: 950px;\n}*/\n/*动画 fade,bounce*/\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity 0.2s;\n}\n.fade-enter-active,\n.fade-leave-active {\n  opacity: 0;\n}\n.bounce-enter-active {\n  animation: bounce-in 0.5s;\n}\n.bounce-leave-active {\n  animation: bounce-out 0.5s;\n}\n@keyframes bounce-in {\n0% {\n    transform: scale(0);\n}\n50% {\n    transform: scale(1.5);\n}\n100% {\n    transform: scale(1);\n}\n}\n@keyframes bounce-out {\n0% {\n    transform: scale(1);\n}\n50% {\n    transform: scale(1.5);\n}\n100% {\n    transform: scale(0);\n}\n}\n.container-moving {\n  width: 950px;\n  margin: 30px auto;\n}\n.container-moving .content {\n  min-height: 420px;\n}\n.container-moving .content h1 {\n  word-break: break-all;\n  displa