r-list .people-sroce .comment-num {\n  margin-top: -20px;\n  margin-left: 45px;\n  color: #666699;\n}\n.container-moving .content .detail .left-side .insterest-people {\n  clear: both;\n  padding: 20px 0 3px;\n}\n.container-moving .content .detail .left-side .insterest-people .top button {\n  display: inline-block;\n  width: 50px;\n  height: 25px;\n  background: #fae9da;\n  border: 1px solid #ca6445;\n  color: #333;\n  margin-right: 10px;\n  border-radius: 4px;\n}\n.container-moving .content .detail .left-side .insterest-people .score-to .el-rate {\n  margin-left: 5px;\n  display: inline-block;\n}\n.container-moving .content .detail .left-side .insterest-people .score-to .el-rate .el-rate__icon {\n  font-size: 12px;\n}\n.container-moving .content .detail .left-side .gtleft {\n  padding: 4px 4px 0 0;\n  float: left;\n}\n.container-moving .content .detail .left-side .gtleft .ul {\n  padding-top: 6px;\n}\n.container-moving .content .detail .left-side .gtleft .ul li {\n  float: left;\n  display: inline;\n  line-height: 14px;\n  margin-right: 15px;\n}\n.container-moving .content .detail .left-side .gtleft .ul li img {\n  max-width: 100%;\n  margin-right: 5px;\n}\n.container-moving .content .detail .left-side .gtleft .ul li a {\n  text-decoration: none;\n  color: #37a;\n}\n.container-moving .content .detail .left-side .summary {\n  float: left;\n  clear: both;\n  margin-top: 20px;\n}\n.container-moving .content .detail .left-side .summary .summary-title {\n  color: #007722;\n  font-size: 16px;\n}\n.container-moving .content .detail .right-side {\n  float: right;\n  width: 310px;\n}\n.container-moving .content .detail .right-side .ticket {\n  padding: 20px 0;\n  margin-bottom: 40px;\n  background-color: #F0F3F5;\n  color: #999;\n  font-size: 13px;\n  text-align: center;\n}\n.container-moving .content .detail .right-side .ticket a {\n  display: inline-block;\n  width: 160px;\n  line-height: 30px;\n  background: #268dcd;\n  border-radius: 2px;\n  color: #fff;\n}\n.container-